import React from "react";

// Required Core Stylesheet
import "../node_modules/@glidejs/glide/dist/css/glide.core.min.css";

// Optional Theme Stylesheet

import "../node_modules/@glidejs/glide/dist/glide.min.js";
import Glide from "@glidejs/glide";

import img1 from './images/image-product-1.jpg'
import img2 from './images/image-product-2.jpg'
import img3 from './images/image-product-3.jpg'
import img4 from './images/image-product-4.jpg'
import imgThumbnail1 from './images/image-product-1-thumbnail.jpg'
import imgThumbnail2 from './images/image-product-2-thumbnail.jpg'
import imgThumbnail3 from './images/image-product-3-thumbnail.jpg'
import imgThumbnail4 from './images/image-product-4-thumbnail.jpg'


const sliderConfiguration = {
  type: "carousel",
  // startAt: 0,
  // animationTimingFunc: "ease-in-out",
  // gap: 250,
  // perView: 3,
};

function Lightbox() {
  const ref = React.useRef();

  React.useEffect(() => {
    const slider = new Glide(ref.current, sliderConfiguration);
    slider.mount();
  }, [ref]);
  
  return (
    <>
    <div className=" flex ">
      <div ref={ref} className="glide max-w-[26rem]  absolute lg:max-w-[30rem] xl:max-w-[35rem] ">
        <div className="glide__track" data-glide-el="track">
          <ul className="glide__slides">
            <li className="glide__slide"><img className="rounded-lg" src={img3} alt="" /></li>
            <li className="glide__slide"><img className="rounded-lg" src={img4} alt="" /></li>
            <li className="glide__slide"><img className="rounded-lg" src={img1} alt="" /></li>
            <li className="glide__slide"><img className="rounded-lg" src={img2} alt="" /></li>
          </ul>
        </div>

        <div className="glide__bullets  flex justify-center gap-7 mt-4" data-glide-el="controls[nav]">
          <button  className="glide__bullet w-24 " data-glide-dir="=0"><img className="rounded-lg border-solid border-2 border-transparent   " src={imgThumbnail1} alt="" /></button>
          <button className="glide__bullet w-24 " data-glide-dir="=1"><img className="rounded-lg border-solid border-2 border-transparent   " src={imgThumbnail2} alt="" /></button>
          <button className="glide__bullet w-24 " data-glide-dir="=2"><img className="rounded-lg border-solid border-2 border-transparent   " src={imgThumbnail3} alt="" /></button>
          <button className="glide__bullet w-24 " data-glide-dir="=3"><img className="rounded-lg border-solid border-2 border-transparent   " src={imgThumbnail4} alt="" /></button>
        </div>

       
      </div>
      </div>
    </>
  );
}

export default Lightbox;
