import img1 from './images/image-product-1.jpg'
import img2 from './images/image-product-2.jpg'
import img3 from './images/image-product-3.jpg'
import img4 from './images/image-product-4.jpg'
import iconNext from './images/icon-next.svg'
import minusSvg from './images/icon-minus.svg'
import plusSvg from './images/icon-plus.svg'
import iconPrevious from './images/icon-previous.svg'
import cartSvg from './images/cart-white.svg'
import hamburgerMenu from './images/icon-menu.svg'
import logo from './images/logo.svg'
import cart from './images/icon-cart.svg'
import avatar from './images/image-avatar.png'
import closeSvg from "./images/icon-close.svg"
import imgThumbnail1 from './images/image-product-1-thumbnail.jpg'
import trashSvg from './images/icon-delete.svg'
import React, { useEffect } from "react"
import Glide from "@glidejs/glide"
import "../node_modules/@glidejs/glide/dist/css/glide.core.min.css"
// import "../node_modules/@glidejs/glide/dist/css/glide.theme.min.css"
import { useState} from 'react'
import Lightbox from './lightbox'



const sliderConfiguration= {
  type: "carousel"
};











const Main = () => {
  const slide = new Glide('.glide', sliderConfiguration);

  useEffect(() => {
    return () => slide.mount()
  }, [slide])
   
  const [count, setCount] = useState(0)
  const [cartCount, setCartCount] = useState(0)

    return (
    <>
    <Navbar cartItems={cartCount}/>
   
      {" "}
      <div className='flex flex-col custommd:flex-row custommd:pt-12 custommd:justify-between custommd:mx-[2rem]  custommd:gap-[2rem] lg:mx-[6rem] xl:mx-[10rem] xxl:mx-[13rem] '>

      <div className='glide relative custommd:w-[30rem] custommd:hidden'>
        <div className='glide__track custommd:w-[30rem]' data-glide-el='track'>
          <ul className='glide__slides grid items-center justify-center custommd:w-[30rem]'>
            <li className='glide__slide slider max-h-[27rem] w-[100vw] custommd:w-[32rem]'><img  src={img1} alt="" /></li>
            <li className='glide__slide slider max-h-[27rem] w-[100vw] custommd:w-[32rem]'><img src={img2} alt="" /></li>
            <li className='glide__slide slider max-h-[27rem] w-[100vw] custommd:w-[32rem]'><img src={img3} alt="" /></li>
            <li className='glide__slide slider max-h-[27rem] w-[100vw] custommd:w-[32rem]'><img src={img4} alt="" /></li>
          </ul>
        </div>

        <div className='glide__arrows   flex justify-between' data-glide-el='controls'>
          <button
            className='glide__arrow glide__arrow--prev absolute top-1/2 ml-5 bg-white w-12 h-12 grid justify-center items-center rounded-full'
            data-glide-dir='<'
          >
            <span><img src={iconPrevious} className="w-3" alt="" /></span>
          </button>
          <button
            className='glide__arrow glide__arrow--next absolute top-1/2 right-0 mr-5  bg-white w-12 h-12 grid justify-center items-center rounded-full'
            data-glide-dir='>'
          >
            <span><img src={iconNext} alt="" /></span>
          </button>
        </div>
      </div>

      <div className="hidden custommd:block">
        <Lightbox/>
      </div>

      <div className="flex flex-col mx-7 mt-6 pb-12 custommd:max-w-[25rem] custommd:pt-12 lg:max-w-[25rem]">
        <h1 className="text-[#e37215] text-lg font-bold tracking-[3px]">SNEAKER COMPANY</h1>
        <h2 className="font-bold text-[#1e2125] text-4xl mt-5">Fall Limited Edition Sneakers</h2>
        <p className="text-lg mt-6 font-semibold  text-[#87898c]">These low-profile sneakers are your perfect causal wear companion.
          Featuring a durable rubber outer sole, they'll withstand everything the weather 
          can offer.
        </p>
        <div className="flex mt-7 justify-between gap-3 items-center">
        <div className="flex items-center gap-4">
          <h3 className='text-[2.1rem] font-bold text-[#1e2125]'>$125.00</h3>
          <span className='ml-4  w-9 h-7 px-7 items-center -mb-1 font-bold flex justify-center py-[.2rem] rounded-md bg-orange-100 text-[#e37215] text-[1.2rem]'>50%</span>
        </div>
        <h3 className='text-[#87898c] -mb-1 font-bold text-[1.2rem] line-through'>$250.00</h3>
      </div>

      <div className=' flex flex-col items-center justify-center lg:flex-row gap-4'>     

        <div className="bg-[#f5f6fa] mt-9 justify-between py-5 rounded-xl flex gap-3 w-[405px] max-h-[60px] custommd:w-[320px] lg:w-[200px] items-center">
        <button onClick={() => {setCount((prevCount) => prevCount < 1 ? 0 : prevCount - 1)}}><img  src={minusSvg} className="w-3 h-1 ml-5" alt="" /></button>
        <h2 className="font-bold text-2xl text-[#1e2125] ">{count}</h2>
        <button onClick={() => {setCount((prevCount) => prevCount + 1)}}><img src={plusSvg} className="mr-5" alt="" /></button>
      </div>

      <button onClick={() => count >= 1 ? setCartCount(count) : null} className="flex bg-[#ff7d1b] px-32  mt-9 justify-center mx-auto py-4 rounded-xl  items-center gap-6 transition-all max-h-[60px]  focus-within:bg-[#f59246] shadow-2xl  shadow-[#ff7d1b] whitespace-nowrap custommd:w-[320px]  lg:px-16 ">
        <span><img src={cartSvg} alt="cart icon" /></span>
        <p className="text-white font-bold text-[1.2rem]">Add to cart</p>
      </button>
      </div>

      </div>
 

      </div>
    </>
  )
}











const Navbar = ({cartItems}) => {
  
  const [clicked, setClicked] = useState(true)
  const [OpenCartSection, setOpenCartSection] = useState(false)
  const [removeItems, setRemoveItems] = useState(false)

  // const removeItems = () => {
  //   cartItems = 0
  // }
    return(
      <>
       <nav className="flex p-8 pt-9 justify-between  lg:mx-12 xl:mx-32">
         <div className="flex gap-6 items-center custommd:flex-row-reverse custommd:space-x-8 custommd:gap-8">
          { clicked ? null : <div className="absolute w-[70vw] py-[27rem]  left-0 z-10  bg-white">
              <div className='ml-6'>
              <img src={closeSvg} className="w-5 mt-52" onClick={() => {setClicked(true)}} alt="" />
              <ul className='pt-12 font-bold space-y-5'>
                  <li><a href="#">Collections</a></li>
                  <li><a href="#">Men</a></li>
                  <li><a href="#">Woman</a></li>
                  <li><a href="#">About</a></li>
                  <li><a href="#">Contact</a></li>
              </ul>
              </div>
              </div> }
  
   
  
           <img onClick={() => {setClicked(false)}} src={hamburgerMenu} className="h-5 custommd:hidden" alt="" />
           <ul className="gap-4 hidden font-semibold text-[#87898c] custommd:flex lg:gap-9">
             <li className="">Collections</li>
             <li className="">Men</li>
             <li className="">Woman</li>
             <li className="">About</li>
             <li className="">Contact</li>
           </ul>
           <img src={logo} className="h-6 -mt-2" alt=""/>
         </div>
         <div className="flex gap-6 items-center">
          <button onClick={() => {setOpenCartSection(true)}} className='relative'><img src={cart} className="w-7" alt="cart items" />{cartItems > 0 ? <span className='absolute -top-3 font-bold -right-2 grid justify-center text-white items-end w-7 rounded-3xl h-5 bg-[#ff7d1b]'><h5 className='-mt-[.1rem]'>{cartItems}</h5></span> : null}</button>
          <button><img src={avatar} className="w-8" alt="cart items"/></button>
         </div>

         {OpenCartSection ? <div className='absolute bg-white w-[27rem] right-[25px] top-28 mx-auto z-20 rounded-lg '>
         {removeItems ? cartItems = 0 : null}
          <div className='py-6 mx-4 flex justify-between'>
          <h1 className='font-bold text-lg'>Cart</h1>
          <button onClick={() => {setOpenCartSection(false)}}>
            <span><img src={closeSvg} alt="" /></span>
          </button>
          </div>
          <div className="border-b-[1px] border-[#d8dbde] "></div>

          

          {removeItems ? <div className="py-20 flex justify-center font-semibold  text-[#87898c] mx-auto"><h1>Your cart is empty.</h1></div> : 
          <>
          <div className='flex mx-8 pb-12 pt-6 items-center justify-between gap-7'>

            <div className=''>
              <img src={imgThumbnail1} className='w-14 rounded-md' alt="" />
            </div>

            <div className='mr-12 text-[#87898c]'>
            <h2>Autumn Limited Edition...</h2>
            <h3>$125.00 x {cartItems} <span className='font-bold text-black '>${125.00 * cartItems}.00</span></h3>
            </div>

            <button onClick={() => {setRemoveItems(true)}} className=''>
            <img src={trashSvg} alt="" />
          </button>
          </div>

          <div className='flex justify-center pb-10'>
          <button className="bg-[#ff7d1b] mx-auto px-32  py-4 rounded-xl    transition-all  focus-within:bg-[#f59246] ">
           <p className="text-white font-bold text-[1.2rem]">Checkout</p>
         </button>
         </div>
          </>
            }
            
         
         </div> : null}
       </nav>
      </>
    )
  }







export default Main;